/* Sephirot â€” SCSS (cyan/black) */
:root {
	--bg: #05070c;
	--panel: rgba(7, 11, 18, .78);
	--panel2: rgba(10, 16, 26, .72);
	--stroke: rgba(70, 255, 255, .18);
	--stroke2: rgba(70, 255, 255, .28);
	--cyan: #30f0ff;
	--cyan2: #0bd4ff;
	--text: #e9f6ff;
	--muted: rgba(233, 246, 255, .72);

	--headerH: 72px;
	--sidebarW: 270px;
	--radius: 18px;
	--shadow: 0 18px 40px rgba(0, 0, 0, .55);
	--glow: 0 0 0 1px rgba(48, 240, 255, .18), 0 0 26px rgba(48, 240, 255, .18);
	--glowStrong: 0 0 0 1px rgba(48, 240, 255, .28), 0 0 44px rgba(48, 240, 255, .24);
}

a {
	color: var(--cyan);
	transition: color ease .25s;
	text-decoration: none;

	&:hover {
		color: var(--cyan2);
	}
}

ul {
	list-style: none;
	padding: 0 !important;

	li {
		margin: .5em 0;
	}
}

* {
	box-sizing: border-box
}

html,
body {
	height: 100%
}

html {
	scroll-behavior: smooth
}

body {
	margin: 0;
	font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial;
	color: var(--text);
	background:
		radial-gradient(1200px 800px at 60% 10%, rgba(48, 240, 255, .10), transparent 55%),
		radial-gradient(900px 600px at 20% 40%, rgba(0, 168, 255, .08), transparent 50%),
		linear-gradient(180deg, var(--bg), #070b14);
	overflow-x: hidden;
	background-attachment: fixed;
}

/* Helpers */
@mixin container {
	width: min(1200px, 100%);
	margin: 0 auto;
}

.container {
	@include container;
}



.hidden {
	display: none !important;
}

/* Fixed Header */
.topbar {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: var(--headerH);
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 18px;
	z-index: 50;
	background: linear-gradient(180deg, rgba(6, 10, 18, .92), rgba(6, 10, 18, .62));
	border-bottom: 1px solid var(--stroke);
	backdrop-filter: blur(10px);

	.topbar-inner {
		display: flex;
		align-items: center;
		gap: 16px;
	}
}

.brand {
	display: flex;
	align-items: center;
	gap: 12px;
	min-width: 260px;

	.brand-mark {
		width: 44px;
		height: 44px;
		border-radius: 14px;
		border: 1px solid var(--stroke2);
		background:
			radial-gradient(circle at 30% 30%, rgba(48, 240, 255, .45), transparent 58%),
			linear-gradient(180deg, rgba(48, 240, 255, .18), rgba(0, 0, 0, .28));
		box-shadow: var(--glow);
	}

	.brand-title {
		b {
			display: block;
			letter-spacing: .10em;
			text-transform: uppercase;
			font-weight: 900;
			font-size: 13px;
		}

		small {
			display: block;
			color: var(--muted);
			letter-spacing: .10em;
			text-transform: uppercase;
			font-size: 11px;
			margin-top: 2px;
		}
	}
}

.nav {
	flex: 1;
	display: flex;
	justify-content: center;
	gap: 10px;

	a {
		color: rgba(233, 246, 255, .78);
		text-decoration: none;
		font-weight: 800;
		letter-spacing: .10em;
		text-transform: uppercase;
		font-size: 12px;
		padding: 10px 12px;
		border-radius: 14px;
		border: 1px solid transparent;
		transition: .18s ease;

		&:hover {
			color: var(--text);
			border-color: rgba(48, 240, 255, .18);
			background: rgba(48, 240, 255, .06);
		}

		&.is-active {
			color: var(--text);
			border-color: rgba(48, 240, 255, .34);
			background: rgba(48, 240, 255, .08);
			box-shadow: var(--glow);
		}
	}
}

.actions {
	display: flex;
	align-items: center;
	gap: 10px;
}

.btn {
	border: none;
	cursor: pointer;
	border-radius: 14px;
	padding: 10px 14px;
	font-weight: 900;
	letter-spacing: .10em;
	text-transform: uppercase;
	font-size: 12px;
	display: inline-flex;
	align-items: center;
	gap: 10px;
	transition: .18s ease;
	user-select: none;
	text-decoration: none;

	&.primary {
		background: linear-gradient(180deg, rgba(48, 240, 255, .24), rgba(48, 240, 255, .10));
		color: var(--text);
		border: 1px solid rgba(48, 240, 255, .30);
		box-shadow: var(--glow);

		&:hover {
			transform: translateY(-1px);
			box-shadow: var(--glowStrong);
		}
	}

	&.ghost {
		background: rgba(255, 255, 255, .04);
		color: rgba(233, 246, 255, .88);
		border: 1px solid rgba(255, 255, 255, .10);

		&:hover {
			border-color: rgba(48, 240, 255, .22);
			background: rgba(48, 240, 255, .06);
		}
	}

	&.frame {
		background: rgba(0, 0, 0, .22);
		border: 1px solid rgba(48, 240, 255, .30);
		box-shadow: var(--glow);
		padding: 12px 18px;
		border-radius: 16px;
		position: relative;

		&::before {
			content: "";
			position: absolute;
			inset: -1px;
			border-radius: 16px;
			background: linear-gradient(90deg, transparent, rgba(48, 240, 255, .28), transparent);
			opacity: .35;
			pointer-events: none;
		}

		&:hover {
			box-shadow: var(--glowStrong);
			transform: translateY(-1px);
		}
	}
}

/* Sidebar */
.sidebar {
	position: fixed;
	top: var(--headerH);
	left: 0;
	width: var(--sidebarW);
	height: calc(100vh - var(--headerH));
	padding: 18px 14px;
	z-index: 40;
	background: linear-gradient(180deg, rgba(6, 10, 18, .92), rgba(6, 10, 18, .74));
	border-right: 1px solid var(--stroke);
	backdrop-filter: blur(10px);
	overflow: auto;

	&::-webkit-scrollbar {
		width: 10px
	}

	&::-webkit-scrollbar-thumb {
		background: rgba(48, 240, 255, .10);
		border-radius: 999px
	}

	&::-webkit-scrollbar-track {
		background: transparent
	}
}

.server-card {
	padding: 14px;
	border-radius: var(--radius);
	background: var(--panel);
	border: 1px solid var(--stroke);
	box-shadow: var(--shadow);
}

.server-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 10px;
	margin-bottom: 8px;
}

.pill {
	font-size: 11px;
	font-weight: 900;
	letter-spacing: .10em;
	text-transform: uppercase;
	padding: 6px 10px;
	border-radius: 999px;
	border: 1px solid rgba(48, 240, 255, .22);
	background: rgba(48, 240, 255, .08);
	box-shadow: var(--glow);
}

.status {
	color: var(--muted);
	font-size: 12px;

	b {
		color: var(--cyan);
		text-shadow: 0 0 12px rgba(48, 240, 255, .35);
	}
}

.side-menu {
	margin-top: 14px;
	display: flex;
	flex-direction: column;
	gap: 8px;

	a {
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 12px 12px;
		border-radius: 14px;
		text-decoration: none;
		color: rgba(233, 246, 255, .80);
		font-weight: 900;
		letter-spacing: .06em;
		text-transform: uppercase;
		font-size: 12px;
		border: 1px solid rgba(255, 255, 255, .08);
		background: rgba(255, 255, 255, .03);
		transition: .18s ease;

		&:hover {
			border-color: rgba(48, 240, 255, .22);
			background: rgba(48, 240, 255, .06);
			transform: translateX(2px);
		}

		&.is-active {
			border-color: rgba(48, 240, 255, .35);
			background: rgba(48, 240, 255, .08);
			box-shadow: var(--glow);
			color: var(--text);
		}
	}
}

/* Main */
.main {
	padding-top: var(--headerH);
	margin-left: var(--sidebarW);
}

.wrap {
	@include container;
	padding: 0 20px;
}

/* Sections */
.section {
	position: relative;
	background: linear-gradient(180deg, rgba(10, 16, 28, .62), rgba(8, 12, 20, .74));
	box-shadow: var(--shadow);
	overflow: hidden;
	scroll-margin-top: calc(var(--headerH) + 16px);

	&.section-landing {}
}

.section-inner {
	position: relative;
	padding: 50px;
}

/* Hero */
.hero {
	padding: 0;
}

.hero-inner {
	display: block;
}

.hero-bg {
	position: absolute;
	inset: 0;
	background:
		linear-gradient(180deg, rgba(0, 0, 0, .40), rgba(0, 0, 0, .62)),
		url("../img/reference-design.png");
	background-size: cover;
	background-position: center top;
	filter: saturate(1.05) contrast(1.06);
}

.hero-bg::after {
	content: "";
	position: absolute;
	inset: 0;
	background:
		radial-gradient(900px 500px at 60% 20%, rgba(48, 240, 255, .18), transparent 55%),
		radial-gradient(900px 600px at 35% 70%, rgba(0, 168, 255, .10), transparent 60%);
	mix-blend-mode: screen;
	opacity: .8;
}


.hero h1 {
	margin: 10px 0 10px 0;
	font-size: clamp(34px, 3.1vw, 54px);
	letter-spacing: .02em;
	line-height: 1.02;
	text-transform: uppercase;

	span {
		color: var(--cyan);
		text-shadow: 0 0 22px rgba(48, 240, 255, .25);
	}
}

.lead {
	margin: 0;
	color: var(--muted);
	font-size: 15px;
	line-height: 1.6;
	max-width: 66ch;
}

.hero-actions {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	margin-top: 16px;
}

.hero-side {
	border-left: 1px solid rgba(48, 240, 255, .12);
	background: rgba(0, 0, 0, .22);
	backdrop-filter: blur(4px);
	display: flex;
	flex-direction: column;
	gap: 12px;
}

/* Cards/grids */
.card {
	border-radius: 18px;
	padding: 14px;
	border: 1px solid rgba(48, 240, 255, .14);
	background: rgba(0, 0, 0, .22);
	box-shadow: 0 10px 26px rgba(0, 0, 0, .35);

	h3 {
		margin: 0 0 8px 0;
		text-transform: uppercase;
		letter-spacing: .10em;
		font-size: 12px;
		color: rgba(233, 246, 255, .90);
	}

	p {
		margin: 0;
		color: var(--muted);
		font-size: 13px;
		line-height: 1.55;
	}
}

.grid-4 {
	display: grid;
	grid-template-columns: repeat(4, minmax(0, 1fr));
	gap: 14px;
}

.grid-3 {
	display: grid;
	grid-template-columns: repeat(3, minmax(0, 1fr));
	gap: 14px;
}

.two-col {
	display: grid;
	grid-template-columns: 1.1fr .9fr;
	gap: 14px;
}

.feature {
	padding: 16px;
	border-radius: 18px;
	border: 1px solid rgba(48, 240, 255, .14);
	background: rgba(0, 0, 0, .18);
	box-shadow: 0 12px 26px rgba(0, 0, 0, .35);
	min-height: 140px;
	position: relative;

	&::before {
		content: "";
		position: absolute;
		inset: -1px;
		border-radius: 18px;
		background: radial-gradient(260px 150px at 20% 0%, rgba(48, 240, 255, .14), transparent 60%);
		opacity: .7;
		pointer-events: none;
	}

	b {
		display: block;
		text-transform: uppercase;
		letter-spacing: .10em;
		font-size: 12px;
		margin-bottom: 8px;
	}

	p {
		margin: 0;
		color: var(--muted);
		font-size: 13px;
		line-height: 1.55;
	}
}

.media {
	border-radius: 18px;
	border: 1px solid rgba(48, 240, 255, .14);
	background:
		linear-gradient(180deg, rgba(0, 0, 0, .30), rgba(0, 0, 0, .55)),
		url("../img/reference-design.png");
	background-size: cover;
	background-position: center 70%;
	min-height: 240px;
	box-shadow: 0 12px 26px rgba(0, 0, 0, .35);
}

.thumb {
	border-radius: 18px;
	border: 1px solid rgba(48, 240, 255, .14);
	background:
		radial-gradient(240px 160px at 20% 10%, rgba(48, 240, 255, .14), transparent 60%),
		rgba(0, 0, 0, .22);
	min-height: 120px;
	box-shadow: 0 12px 26px rgba(0, 0, 0, .35);
}

.footer {
	color: rgba(233, 246, 255, .65);
	font-size: 12px;
	text-align: center;
	padding: 24px 0 40px;
}

/* Mobile */
.burger {
	display: none;
	width: 44px;
	height: 44px;
	border-radius: 14px;
	border: 1px solid rgba(48, 240, 255, .18);
	background: rgba(48, 240, 255, .06);
	color: var(--text);
	box-shadow: var(--glow);
	cursor: pointer;
}

@media (max-width: 980px) {
	:root {
		--sidebarW: 0px;
	}

	.brand {
		min-width: auto
	}

	.sidebar {
		transform: translateX(-110%);
		transition: .2s ease;
		width: 270px;
	}

	.sidebar.is-open {
		transform: translateX(0)
	}

	.main {
		margin-left: 0
	}

	.wrap {
		padding: 18px
	}

	.hero-inner {
		grid-template-columns: 1fr
	}

	.hero-side {
		border-left: none
	}

	.grid-4 {
		grid-template-columns: repeat(2, minmax(0, 1fr));
	}

	.two-col {
		grid-template-columns: 1fr
	}

	.grid-3 {
		grid-template-columns: 1fr
	}

	.nav {
		display: none
	}

	.burger {
		display: inline-flex;
		align-items: center;
		justify-content: center;
	}
}

.hero-slider-item {
	width: 100%;
}

.user-panel-card {
	margin-top: 14px;
}


header.topbar {
	&:before {
		content: '';
		width: 100%;
		height: 5px;
		position: absolute;
		bottom: 0px;
		left: 50%;
		transform: translateX(-50%) translateY(50%);
		background: #000000;
		background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(2, 215, 247, 1) 50%, rgba(0, 0, 0, 0) 100%);
	}

	&::after {
		content: '';
		width: 50%;
		height: 5px;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		background: rgba(2, 215, 247, 1);
		border-radius: 100%;
		filter: blur(6px);
	}
}

section {
	margin: 20px 0;

	&#home {
		text-align: center;
		background-image: url('../img/sections/home-section-bg.png');
		background-size: cover;
		background-position: top center;
		text-shadow: 0 0 20px black;
	}
}

.button-row {
	text-align: center;

	.play-now-button {
		display: inline-block;
		max-width: 400px;
		transform-origin: center center;
		transition: transform ease .3s, filter ease .3s, opacity ease .3s;
		opacity: .7;

		img {
			display: block;
			width: 100%;
		}

		&:hover {
			transform: scale(1.05);
			filter: drop-shadow(0 0 10px cyan);
			opacity: 1;
		}
	}
}

.hero-slider {
	border: 4px solid;
	border-image: linear-gradient(92deg, #080c15, #28bded, #080c15) 1;
	box-shadow: 0 0 10px cyan;
	position: relative;

	&:after {
		content: "";
		position: absolute;
		inset: -1px;
		background: radial-gradient(100% 150px at 50% 0, rgba(48, 240, 255, .14), transparent 60%);
		opacity: .7;
		pointer-events: none;
	}

	.flickity-page-dots {
		bottom: 10px;

		.dot {
			opacity: .7;
			background-color: white;
			border-radius: 0;
			margin: 0 4px;

			&.is-selected {
				opacity: 9;
			}
		}
	}
}

.section-row {
	.section-row-inner {
		display: flex;
		gap: 4%;
		flex-wrap: wrap;
		align-items: center;

		.section-column {
			width: 100%;

			.hero-slider-item-inner {
				img {
					width: 100%;
				}
			}

			@media only screen and (min-width: 989px) {
				width: 48%;
			}
		}
	}
}